/*------------------- app -------------------*/

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  
}

.btn-height-22{
  height: 22px !important;
}

.theme--light > .v-application--wrap{
  background-color: #eee;
}

/*------------------- end app -------------------*/

/*------------------- input -------------------*/

label.theme--dark + input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active {
  -webkit-text-fill-color: #fff;
  -webkit-box-shadow: 0 0 0px 1000px #1e1e1e inset;
}

label.theme--light + input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-text-fill-color: #000;
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset;
}

/*------------------- end input -------------------*/

/*------------------- scrollbar -------------------*/

.v-list.schedule-list::-webkit-scrollbar,
.v-navigation-drawer__content::-webkit-scrollbar,
.v-navigation-drawer__content > div::-webkit-scrollbar,
.v-data-table__wrapper::-webkit-scrollbar,
.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar,
.v-dialog__content > .v-dialog::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar{
  width: 5px;
  height: 8px;
}

/*---dark ---*/

.theme--dark.v-list.schedule-list::-webkit-scrollbar-track,
.theme--dark > .v-navigation-drawer__content::-webkit-scrollbar-track,
.theme--dark > .v-navigation-drawer__content > div::-webkit-scrollbar-track,
.theme--dark > .v-data-table__wrapper::-webkit-scrollbar-track,
.theme--dark > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-track, 
.theme--dark.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-track,
.theme--dark > .v-dialog__content > .v-dialog::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.theme--dark.v-list.schedule-list::-webkit-scrollbar-thumb,
.theme--dark > .v-navigation-drawer__content::-webkit-scrollbar-thumb,
.theme--dark > .v-navigation-drawer__content > div::-webkit-scrollbar-thumb,
.theme--dark > .v-data-table__wrapper::-webkit-scrollbar-thumb,
.theme--dark > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-thumb,
.theme--dark.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-thumb,
.theme--dark > .v-dialog__content > .v-dialog::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.theme--dark.v-list.schedule-list::-webkit-scrollbar-thumb:hover,
.theme--dark > .v-navigation-drawer__content::-webkit-scrollbar-thumb:hover,
.theme--dark > .v-navigation-drawer__content > div::-webkit-scrollbar-thumb:hover,
.theme--dark > .v-data-table__wrapper::-webkit-scrollbar-thumb:hover,
.theme--dark > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-thumb:hover, 
.theme--dark.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-thumb:hover,
.theme--dark > .v-dialog__content > .v-dialog::-webkit-scrollbar-thumb:hover {
  background: #eee;
}

/*--- light ---*/

.theme--light.v-list.schedule-list::-webkit-scrollbar-track,
.theme--light > .v-navigation-drawer__content::-webkit-scrollbar-track,
.theme--light > .v-navigation-drawer__content > div::-webkit-scrollbar-track,
.theme--light > .v-data-table__wrapper::-webkit-scrollbar-track,
.theme--light > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-track,
.theme--light.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-track,
.theme--light> .v-dialog__content > .v-dialog::-webkit-scrollbar-track {
  background: #eee;
  border-left: 1px solid #ffffff;
}

.theme--light.v-list.schedule-list::-webkit-scrollbar-thumb,
.theme--light > .v-navigation-drawer__content::-webkit-scrollbar-thumb,
.theme--light > .v-navigation-drawer__content > div::-webkit-scrollbar-thumb,
.theme--light > .v-data-table__wrapper::-webkit-scrollbar-thumb,
.theme--light > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-thumb,
.theme--light.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-thumb,
.theme--light> .v-dialog__content > .v-dialog::-webkit-scrollbar-thumb {
  background-color: #66666633;
  border: solid 2px #ffffff;
  border-radius: 6px;
}

.theme--light.v-list.schedule-list::-webkit-scrollbar-thumb:hover,
.theme--light > .v-navigation-drawer__content::-webkit-scrollbar-thumb:hover,
.theme--light > .v-navigation-drawer__content > div::-webkit-scrollbar-thumb:hover,
.theme--light > .v-data-table__wrapper::-webkit-scrollbar-thumb:hover,
.theme--light > .v-data-table__wrapper > table > tbody > tr > td > .overflow-td::-webkit-scrollbar-thumb:hover,
.theme--light.v-window > .v-window__container > .v-window-item > .v-card__text > .container > div::-webkit-scrollbar-thumb:hover,
.theme--light> .v-dialog__content > .v-dialog::-webkit-scrollbar-track:hover {
  background: #666666;
}

/*------------------- end scrollbar -------------------*/

/*------------------- table------------------- */

.overflow-td {
  overflow-y: auto;
  width: 200px;
  height: 66px;
  word-break: break-word;
}

.value {
  font-weight: bold;
  opacity: 0.6;
}

.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}

.centered-input>>>input {
  text-align: center;
}

.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}

/*------------------- end table -------------------*/
